<template lang="html">
  <div class="pay-complete-page">
    <!-- 头部 -->
    <default-head header="支付成功"></default-head>
    <pay-complete-back></pay-complete-back>
    <p class="warn">恭喜您已支付成功！</p>
    <div class="pay-complete-bottom clearfix">
      <span class="fl">查看详情</span>
      <span class="fl">再逛一会</span>
    </div>
  </div>
</template>

<script>
import defaultHead from '@/components/head/defaultHead'
import payCompleteBack from '@/components/payComplete/payCompleteBack'

export default {
  data () {
    return {
    }
  },
  components: { defaultHead, payCompleteBack }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.pay-complete-page {
  padding-top: .88rem;
  .warn {
    font-size: .32rem;
    height: .88rem;
    line-height: .88rem;
    text-align: center;
    background: @whiteColor;
    border-bottom: .01rem solid @lineColor;
  }
  .pay-complete-bottom {
    background: @whiteColor;
    span {
      width: 50%;
      text-align: center;
      font-size: .32rem;
      line-height: .32rem;
      margin: .28rem 0;
      &:first-child {
        color: @activeColor;
        border-right: .01rem solid @lineColor;
      }
    }
  }
}

</style>



